.header {
  display: flex;
  justify-content: space-between;
  height: 4em;
  padding: 0 1em;
  background-color: #111;
}

.logo {
  display: flex;
  align-items: center;
  padding: 0 1em;
  cursor: pointer;
}

.logo img {
  width: 20%;
  margin-right: 0.5em;
}

.logo h1 {
  font-family: 'Antonio', sans-serif;
  color: #eee;
  font-size: 1.5rem;
  letter-spacing: -0.02em;
}

.searchbar {
  display: flex;
  align-items: center;
  width: 80%;
}

.searchbar > * {
  height: 50%;
}

.searchbar input {
  flex-basis: 70%;
  padding: 0 0.5em;
  font-size: 1em;
  border-radius: 2px 0 0 2px;
  box-shadow: inset 0 1px 2px #eee;
}

.searchbar button {
  flex-basis: 5%;
  padding: 0.5em;
  background-color: #777;
  border-radius: 0 2px 2px 0;
  cursor: pointer;
  transition: all 200ms ease-in;
}

.searchbar button:hover {
  background-color: #666;
}

.searchbar button img {
  width: 30%;
}

/* Responsive - Tablet */
@media screen and (max-width: 1023px) {
  .searchbar {
    width: 50%;
  }

  .searchbar input {
    flex-basis: 100%;
  }

  .searchbar button {
    flex-basis: 10%;
  }

  .searchbar button img {
    width: 50%;
  }
}

/* Responsive - Smartphone */
@media screen and (max-width: 767px) {
  .logo img {
    width: 30%;
    margin-right: 0.2em;
  }

  .logo h1 {
    font-size: 1.2rem;
  }

  .searchbar {
    width: 80%;
  }

  .searchbar input {
    flex-basis: 100%;
    max-width: 85%;
  }

  .searchbar button {
    flex-basis: 10%;
    min-width: 15%;
  }
}

/* Responsive - iPhone SE */
@media screen and (max-width: 320px) {
  .searchbar {
    width: 60%;
  }

  .searchbar button img {
    width: 100%;
  }
}
